@import url(https://fonts.googleapis.com/css?family=Oxygen&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: Oxygen, sans-serif;
  scroll-behavior: smooth;
  font-size: 100%;
  overflow-x: hidden;
}
/* nav */
li a {
  display: block;
  text-decoration: none;
  color: #fff;
  padding: 1rem 1rem;
}
li > a:hover {
  color: orange;
  background: rgb(22, 79, 117);
}

/*HEADER STYLE*/

.btn {
  margin: auto;
  margin-bottom: 30px;
  border: 2px solid white;
  width: 10em;
  height: 2.4em;
  font-family: Oxygen, sans-serif;
  font-size: 1rem;
  color: #fff;
  cursor: pointer;
  border-radius: 40px;
  background-color: #227093;
  -webkit-transition-property: border, background-color;
  transition-property: border, background-color;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-in-out;
          transition-timing-function: ease-in-out;
}

.btn:hover {
  border-width: 4px;
  background-color: transparent;
}

li {
  list-style: none;
  color: black;
}

.section {
  display: flex;
  width: 100%;
}

/* Skills */
.experience-sec {
  flex-direction: column;
  text-align: center;
  font-family: Oxygen, sans-serif;
}

.section-header {
  margin: auto;
  margin-top: 2em;
  font-size: 2em;
  padding: 10px 5px;
  border-bottom: 3px solid #33d9b2;
}

.experience-container {
  display:flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  margin-top: 4em;
}

.experience-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 35px;
  border-radius: 4px;
}

.item-logo > img {
  width: 100%;
  height: auto;
}

.item-logo {
  width: 13em;
  height: auto;
}

.item-name {
  font-size: 1.3em;
  margin-top: 0.5em;
}

/* About Me */
.about-me {
  margin: auto;
  color: white;
  font-size: 1.2em;
  background-color: #227093;
  flex-direction: column;
}

.about-me > .section-header {
  font-size: 2rem;
}

.about-me  p {
  margin: 0;
  flex-wrap: wrap;
  margin-bottom: 1em;
}

.profile-container {
  text-align: left;
  margin-top: 2em;
  padding: 1em 2em;
}

.profile-container > p {
  padding: 0 1em;
}

.profile-pic {
  float: left;
  max-width: 200px;
  height: auto;
  border-radius: 25%;
  margin: 0 2em;
  margin-bottom: 2em;
 }

/* Contacts */

.contact-sec {
  min-height: 30em;
  background-color: #227093;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
}

.contact-container{
  z-index: 0;
  width: 21em;
  margin-top: 5em;
  display: flex;
  align-items: center;
}

#contact-form {
  display: flex;
  flex-direction: column;
  width: 100%;
  font-family: Oxygen, sans-serif;
}

input {
  border: solid 1px rgb(169, 169, 169);
}

#contact-form > input, #contact-form > textarea {
  margin-bottom: 30px;
  margin-top: 10px;
  border-bottom: 3px solid #33d9b2;
  box-shadow: 0px 0px 15px 4px rgba(15, 15, 15, 0.35);
  font-family: Oxygen, sans-serif;
}

#contact-form > input {
  padding: 0 5px;
  height: 37px;
}

#contact-form > textarea {
  max-width: 100%;
  min-width: 90%;
  max-height:50vh;
  min-height:25vh;
}

#contact-form > textarea::-webkit-input-placeholder {
  max-width: 100%;
  min-height:100%;
  margin-bottom: 2rem;
  font-size: 0.8rem;
  height: 60px;
  font-family:  Oxygen, sans-serif;
}

#contact-form > textarea::-moz-placeholder {
  max-width: 100%;
  min-height:100%;
  margin-bottom: 2rem;
  font-size: 0.8rem;
  height: 60px;
  font-family:  Oxygen, sans-serif;
}

#contact-form > textarea:-ms-input-placeholder {
  max-width: 100%;
  min-height:100%;
  margin-bottom: 2rem;
  font-size: 0.8rem;
  height: 60px;
  font-family:  Oxygen, sans-serif;
}

#contact-form > textarea::-ms-input-placeholder {
  max-width: 100%;
  min-height:100%;
  margin-bottom: 2rem;
  font-size: 0.8rem;
  height: 60px;
  font-family:  Oxygen, sans-serif;
}

#contact-form > textarea::placeholder {
  max-width: 100%;
  min-height:100%;
  margin-bottom: 2rem;
  font-size: 0.8rem;
  height: 60px;
  font-family:  Oxygen, sans-serif;
}

/* Footer */
.footer {
  background: rgb(33, 93, 133);
}

.footer-item {
  display: inline-flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding:10px 10px;
}

.footer > p {
  color: white;
  width:100%;
  margin: auto;
  text-align: center;
  padding-top: 5px;
}

.footer-item-container {
  display: flex;
  justify-content: center;
}

@media only screen and (max-width: 650px) {
  .profile-pic {
    margin: 0 1rem;
  }
  .profile-container {
    padding-left: 3em;
    padding-right: 1em;
  }
  html {
    font-size:80%;
  }
}

